//  重複套用CLASS設定結束
body {
    font-family: 'Roboto', Arial, Helvetica, sans-serif, "微軟正黑體";
    font-size: 24px;
    line-height: 1.5;
    font-style: italic;
}

header {
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("../images/banner.png");
    height: 958px;
    background-size: cover;
    font-weight: bold;

    .title {
        display: block;
        line-height: 106px;
        font-size: 80px;

    }

    .subtitle {
        display: block;
        line-height: 42px;
    }

    .header-content {
        display: flex;
        flex-direction: column;
    }

    .social-icon a:hover img {
        transform: scale(1.1);
    }

}

.about {
    font-weight: bold;

    h2 {
        font-size: 64px;
        line-height: 84px;
    }

    .about-me li:nth-child(-n+2) {
        line-height: 42px;
    }

    .my-work-ex {
        border-top: 1px solid #000;
        width: 384px;
        margin: 0 auto;

        li {
            line-height: 42px;
        }

        li::before {
            content: "";
            display: inline-block;
            width: 22px;
            height: 22px;
            margin-right: 16px;
            background-color: #891818;
            border-radius: 50%;
        }

    }

}

.myBrand {
    background-color: #000;

    h2 {
        font-size: 68px;
        line-height: 84px;
    }

    h3 {
        line-height: 42px;
    }

    p {
        line-height: 30px;
        font-size: 20px;
    }

    .myBrand-pic {
        flex-wrap: wrap;
        justify-content: center;
    }
}

.otherWorks {

    h2 {
        font-size: 64px;
        line-height: 84px;
        line-height: 84px;
    }
    p{
        font-size: 20px;
        line-height: 30px;
    }
    .work-pic{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}
footer{
    background-color: #000;
    h2{
        font-size: 64px;
        line-height: 84px;
    }

    a{
        color: white;
    }

    a:hover{
        text-decoration: underline;
    }
   
   

}